import React from "react";
import Logo from "./Logo";
import { Link } from "gatsby";
import styled from 'styled-components';
import { useStaticQuery, graphql } from "gatsby"

const NavStyles = styled.nav`
  .sub-menu{display:none;}

  ul {
    list-style: none;
    display: flex;
    align-content: center;
    align-item: center;
    li {
      padding-right: 25px;
      padding-top: 10px;
      a {
        font-family: 'Montserrat-Regular';
        font-size: 16px;
        color: #fff;
        display: inline-block;
        transition: all 0.5s ease;
        position: relative;
        text-transform: uppercase;
        padding-bottom: 14px;
      }
      a::before {
        background: #be283c;
        content: "";
        position: absolute;
        left: 50%;
        bottom: 3px;
        transform: translatex(-50%);
        width: 0%;
        height: 1px;
        transition: all 0.4s;
      }

      a::after {
        content: "";
        position: absolute;
        left: 50%;
        width: 7px;
        height: 7px;
        background: #be283c;
        bottom: 0;
        opacity: 0;
        visibility: hidden;
        transition: all 0.2s ease;
      }

      .sub-menu {
        position: absolute;
        left: 0;
        background-color: #be283c;
        width: 240px;
        display: none;
        top: 34px;
        li {
          width: 100%;
          margin: 0;
          padding: 0;
          a {
            font-size: 12px;
            padding: 7px 18px;
            border-bottom: 1px solid;
            width: 100%;
            line-height: 16px;
          }
        }
      }
    }
    li::hover{
      display:block;
    }
  }
`;
 
export default function Nav() {

  return (
    <div className="container">
      <Logo />
      </div>
    );
}